import { Injectable } from '@angular/core';
import { WindowVariableService } from '../window-service/window-variable.service';

interface TabEntity {
    tabId: string;
    code?: string;
    name?: string;
    appType: string;
    funcId: string;
    appId: string;
    appEntrance: string;
}

@Injectable({
    providedIn: 'root'
})
export class TabEntityService {

    private nameSpace = 'gsp-rtf-tabarr-service';
    private tabarrayKey = 'tab-arry';
    private windowService: WindowVariableService;
    private topWin: Window;


    constructor() {
        this.windowService = new WindowVariableService();
        this.topWin = this.windowService.getTopWindow();
        this.topWin[this.nameSpace] = this.topWin[this.nameSpace] || {};
    }

    public setTabs(tabs: any[]) {
        if (!tabs || tabs.length === 0) {
            return;
        }
        const copy = this.deepClone(tabs);
        this.topWin[this.nameSpace][this.tabarrayKey] = copy;
    }

    public getTabs(): any[] {
        const tabs = this.topWin[this.nameSpace][this.tabarrayKey] || [];
        if (!tabs || tabs.length === 0) {
            return tabs;
        }
        return this.deepClone(tabs);
    }

    public setActiveTab(tab: any) {
        const tabs: any[] = this.getTabs();
        const activeOld = tabs.find(t => t.active);
        if (!tab) {
            if (!!activeOld) {
                activeOld.active = false;
            }
            return;
        }

        if (tabs.length > 0) {
            let activeNew = tabs.find(t => t.id === tab.id);
            if (!activeNew) {
                activeNew = tab;
                if (!!activeOld) {
                    activeOld.active = false;
                }
                activeNew.active = true;
                tabs.push(activeNew);
            } else {
                if (!!activeOld) {
                    if (activeOld === activeNew) {
                        return;
                    } else {
                        activeOld.active = false;
                        activeNew.active = true;
                    }
                } else {
                    activeNew.active = true;
                }
            }
            this.setTabs(tabs);
        }
    }

    public getActiveTab(): any {
        const tabs: any[] = this.getTabs();
        const activeTab = tabs && tabs.find(t => t.active);
        const res: TabEntity = activeTab && {
            tabId: activeTab.id,
            appType: activeTab.appType,
            funcId: activeTab.funcId,
            appId: activeTab.appId,
            appEntrance: activeTab.appEntrance,
            name: activeTab.FuncName
        };
        return res;
    }

    private deepClone(target: any): any {
        let result;
        if (typeof target === 'object') {
            if (Array.isArray(target)) {
                result = [];
                for (let i in target) {
                    result.push(this.deepClone(target[i]))
                }
            } else if (target === null) {
                result = null;
            } else if (target.constructor === RegExp) {
                result = target;
            } else {
                result = {};
                for (let i in target) {
                    result[i] = this.deepClone(target[i]);
                }
            }
        } else {
            result = target;
        }
        return result;
    }
}
