import { Injectable } from '@angular/core';
import { WindowVariableService } from '../window-service/window-variable.service';
import { CommonUtil } from '../../utilities/common.utilities';


@Injectable()
export class SessionService {
    private SessionKey = 'session';
    private SessionKeyForIde = 'sessionId';
    private FuncSessionKey = 'FuncSession';
    private windowService: WindowVariableService;

    constructor() {
        window[this.FuncSessionKey] = window[this.FuncSessionKey] || new Map<string, string>();
        this.windowService = new WindowVariableService();
    }

    /**
     * 获取用户sessionid
     */
    getUserSessionId(): string {
        return localStorage.getItem(this.SessionKey) || '';
    }

    /**
     * 获取当前session 之前依赖router的方式改掉
     * 从url hash中获取query
     * N 栈为funcSession
     * J 栈为formToken
     */
    getCurrentSeesionId(tabId?: string): string {
        if (!!tabId) {
            const tab = this.getTabArray().find(t => t.id === tabId);
            return tab && (tab.formToken || tab.sessionId);
        }
        let ssid = CommonUtil.getQueryStringFromHash('cvft');
        if (!ssid) {
            const pageId = CommonUtil.getQueryStringFromHash('tabId');
            const tab = this.getTabArray().find(t => t.id === pageId);
            if (tab) {
                ssid = (tab.formToken || tab.sessionId);
            }
        }
        return ssid || this.getUserSessionId();
    }

    /***
     * 获取功能的session(功能重复打开，会存在多个)
     * (1912 框架移除funcSession，接口作废)
     */
    getFuncSessionId(funcId: string) {
        const ssMap = new Map<string, string>();
        const tabs = this.getTabArray().filter(t => t.funcId === funcId);
        if (!tabs || tabs.length === 0) {
            return ssMap;
        }
        // tslint:disable-next-line: prefer-for-of
        for (let i = 0; i < tabs.length; i++) {
            ssMap.set(tabs[i].tabId, tabs[i].sessionId);
        }

        return ssMap;
    }

    /**
     * 获取应用session（应用重复打开会存在多个）
     * (1912 框架移除funcSession，接口作废)
     */
    getAppSessionId(appId: string, appEntrance: string) {
        const ssMap = new Map<string, string>();
        const tabs = this.getTabArray().filter(t => t.appId === appId && t.appEntrance === appEntrance);
        if (!tabs || tabs.length === 0) {
            return ssMap;
        }
        // tslint:disable-next-line: prefer-for-of
        for (let i = 0; i < tabs.length; i++) {
            ssMap.set(tabs[i].tabId, tabs[i].sessionId);
        }
        return ssMap;
    }

    /**
     * 设置用户sessionid
     * 只是用于N版
     */
    setUserSessionId(ssid: string) {
        // todo框架修改之后，要和IDE同步
        localStorage.setItem(this.SessionKey, ssid);
        localStorage.setItem(this.SessionKeyForIde, ssid);
    }

    /**
     * 缓存当前功能的sessionid
     * (1912 框架移除funcSession，接口作废)
     */
    setFuncSessionId(key: string, ssid: string) {
        this.getFuncSessionCache().set(key, ssid);
    }

    /**
     * 缓存当前应用的sessionId
     * (1912 框架移除funcSession，接口作废)
     */
    setAppSessionId(appId: string, appEntrance: string, ssid: string) {
        this.setFuncSessionId(`${appId}#${appEntrance}`, ssid);
    }

    private getFuncSessionCache(): Map<string, string> {
        const sessionValue = this.windowService.getVariableFromWindow(this.FuncSessionKey);
        return sessionValue as Map<string, string>;
    }

    private getTabArray(): any[] {
        const iframeTabarray = 'iframeTabarray';
        const iframArr = this.windowService.getArrayFromWindow(iframeTabarray);
        return iframArr;
    }

}
