import { Injectable } from '@angular/core';

@Injectable()
export class WindowVariableService {

    constructor() { }

    /**
     * 从当前window遍历到框架顶级window
     * 并获取一个非空的自定义变量
     */
    public getVariableFromWindow(key: string): any {
        const topWin = this.getTopWindow();
        let frmWin: Window = window;
        while (!frmWin[key] && frmWin !== topWin) {
            frmWin = frmWin.parent;
        }
        return frmWin[key];
    }

    /**
     * 从当前window遍历到框架顶级window
     * 并获取一个非空且含有元素的自定义数组变量
     */
    public getArrayFromWindow(key: string): any[] {
        const topWin = this.getTopWindow();
        let frmWin: Window = window;
        while ((!frmWin[key] || frmWin[key].length === 0) && frmWin !== topWin) {
            frmWin = frmWin.parent;
        }
        return frmWin[key] as any[] || [];
    }

    /**
     * 获取框架的顶级window
     * 进行跨域封装
     */
    public getTopWindow(): Window {
        let curWin: Window = window.self;
        try {
            while (curWin.location.origin === curWin.parent.location.origin) {
                if (curWin !== curWin.parent && !(curWin as any).isRTFTopWin) {
                    curWin = curWin.parent;
                } else {
                    break;
                }
            }
        } catch (e) { }
        return curWin;
    }
}
